  --switcher-status-shadow: 1px 1px 2px #0000001a, 0px 2px 2px #0000001a, 1px 2px 4px #0000001a;
  --secondary-light: #e0e0e0;
  --primary-akzent: #4f2ee8;
  --secondary-akzent: #eac645;
  --default-primary-color: #fff;
  --default-secondary-color: #111;
  --user-bg: #f6f6f6;
  --secondary-text-color: #1119;
  --test-secondary-text-color-darkt: #fff9;
}

